.section {
  width: 100%;
  padding-top: 80px;
  padding-bottom: 80px;
}

.container {
  max-width: 1164px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

body {
  font-family: "Rubik", sans-serif;
}

h1 {
  color: #242A45;
  font-size: 48px;
  font-weight: 500;
  line-height: 52px;
}

h2 {
  color: #242A45;
  font-size: 32px;
  font-weight: 500;
  line-height: 52px;
}

h3 {
  color: #242A45;
  font-size: 32px;
  font-weight: 500;
  line-height: 52px;
}

p {
  color: #242A45;
  font-size: 18px;
  font-weight: 400;
  line-height: 28px;
  opacity: 0.5;
}

a {
  text-decoration: none;
  color: #242A45;
}

.button__primary {
  background-color: #5267DF;
  padding: 10px 24px;
  box-shadow: 0px 8px 8px -4px rgba(73, 93, 207, 0.20012);
  border-radius: 5px;
  border: 2px solid #5267DF;
  cursor: pointer;
  display: block;
  color: white;
  letter-spacing: 0.25px;
  transition: 0.3s;
  line-height: 28px;
}
.button__primary:hover {
  background: #FFFFFF;
  color: #5267DF;
}

.button__secondary {
  padding: 10px 24px;
  background: #F7F7F7;
  box-shadow: 0px 8px 8px -4px rgba(73, 93, 207, 0.20012);
  border-radius: 5px;
  border: 2px solid #F7F7F7;
  cursor: pointer;
  display: block;
  color: #242A45;
  letter-spacing: 0.25px;
  transition: 0.3s;
  opacity: 0.75;
  line-height: 28px;
}
.button__secondary:hover {
  background: #FFFFFF;
  border: 2px solid #5B6074;
}

@media only screen and (max-width: 428px) {
  h1 {
    font-size: 30px;
    line-height: 40px;
  }
  h2 {
    font-size: 24px;
    line-height: 52px;
  }
  p {
    font-size: 15px;
    line-height: 25px;
  }
  .section {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 50px;
  padding-bottom: 30px;
}
.header__wrapper .header__logo .mobile-logo {
  display: none;
}
.header__wrapper .header__navs .nav__items {
  display: flex;
  flex-direction: row;
  gap: 48px;
}
.header__wrapper .header__navs .nav__items li {
  list-style: none;
}
.header__wrapper .header__navs .nav__items a {
  font-size: 13px;
  letter-spacing: 1.5px;
}
.header__wrapper .header__navs .nav__items > :last-child a {
  color: white;
  background-color: #FA5959;
  padding: 12px 34px;
  box-shadow: 0px 8px 8px -4px rgba(73, 93, 207, 0.20012);
  border-radius: 5px;
  border: 2px solid #FA5959;
}
.header__wrapper .header__navs .nav__items > :last-child a:hover {
  background: transparent;
  transition: 0.3s;
  color: #FA5959;
}
.header__wrapper .header__navs .socials {
  display: none;
}
.header__wrapper .header__hamburger {
  display: none;
}
.header__wrapper .header__hamburger div {
  width: 18px;
  height: 3px;
  background-color: black;
  border-radius: 20px;
}
.header__wrapper .header__hamburger div + div {
  margin-top: 3px;
}

header {
  z-index: 100;
}

header.stickyHeader {
  position: fixed;
  width: 100%;
  top: 0;
  background-color: white;
  box-shadow: 0px 0px 27px rgba(0, 0, 0, 0.06);
}
header.stickyHeader .header__wrapper {
  padding-top: 30px;
  transition: 0.3s;
}

@media only screen and (max-width: 768px) {
  .header__wrapper .header__navs {
    display: none;
  }
  .header__wrapper .header__hamburger {
    display: block;
  }
  .header__wrapper.openmenu {
    position: relative;
    height: 100vh;
    align-items: baseline;
  }
  .header__wrapper.openmenu .header__logo .desktop-logo {
    display: none;
  }
  .header__wrapper.openmenu .header__logo .mobile-logo {
    display: block;
  }
  .header__wrapper.openmenu .header__navs {
    position: absolute;
    display: block;
    top: 13%;
    left: 0;
    width: 100%;
  }
  .header__wrapper.openmenu .header__navs .nav__items {
    flex-direction: column;
    padding-left: 0;
    gap: 24px;
  }
  .header__wrapper.openmenu .header__navs .nav__items > :last-child {
    padding-top: 40px;
  }
  .header__wrapper.openmenu .header__navs .nav__items li {
    text-align: center;
    position: relative;
    padding-top: 24px;
  }
  .header__wrapper.openmenu .header__navs .nav__items li:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: white;
    opacity: 0.15;
  }
  .header__wrapper.openmenu .header__navs .nav__items li a {
    color: white;
  }
  .header__wrapper.openmenu::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #242A45;
    opacity: 0.95;
    z-index: -5;
  }
  .header__wrapper.openmenu .header__hamburger div {
    background-color: white;
  }
}
